// üî• Í∏∞Í∞ÄÏ∞®Îìú Ïï± ÏÑ§Ï†ï ÏÑπÏÖò - ÏµúÏ†ÅÌôî
'use client';

import React, { useCallback, useEffect, useState } from 'react';
import { Settings } from 'lucide-react';
import { SETTINGS_PAGE_STYLES } from '../../constants/styles';
import { SettingItem } from '../controls/SettingItem';
import { Toggle } from '../controls/Toggle';
import { useTheme } from '../../../../providers/ThemeProvider';
import { useDynamicFont } from '../../../../hooks/useDynamicFont';
import { Logger } from '../../../../../shared/logger';
import GoogleAccountActions from '../GoogleAccountActions';
import type { SettingsData, UpdateSettingFunction } from '../../types';

/**
 * üî• Ïï± ÏÑ§Ï†ï ÏÑπÏÖò Props
 */
interface AppSettingsSectionProps {
  settings: SettingsData['app'];
  updateSetting: UpdateSettingFunction;
  setTheme: (theme: 'light' | 'dark' | 'system') => void;
}

/**
 * üî• Ïï± ÏÑ§Ï†ï ÏÑπÏÖò Ïª¥Ìè¨ÎÑåÌä∏
 */
export const AppSettingsSection = React.memo<AppSettingsSectionProps>(({
  settings,
  updateSetting,
  setTheme
}) => {
  const { theme: currentTheme } = useTheme();
  const { availableFonts, loading: fontsLoading, error: fontsError, setFont } = useDynamicFont();

  // üî• Î°úÏª¨ ÌÖåÎßà ÏÉÅÌÉú (ÏÑ§Ï†ï UI ÌëúÏãúÏö©)
  const [displayTheme, setDisplayTheme] = useState<'light' | 'dark' | 'system'>(settings.theme);

  // üî• ÏÑ§Ï†ïÏù¥ Î≥ÄÍ≤ΩÎêòÎ©¥ Î°úÏª¨ ÏÉÅÌÉú ÎèôÍ∏∞Ìôî
  useEffect(() => {
    setDisplayTheme(settings.theme);
  }, [settings.theme]);

  // üî• ThemeProviderÏùò ÌÖåÎßàÍ∞Ä Î≥ÄÍ≤ΩÎêòÎ©¥ ÎèôÍ∏∞Ìôî
  useEffect(() => {
    if (currentTheme !== displayTheme && currentTheme !== 'system') {
      setDisplayTheme(currentTheme);
    }
  }, [currentTheme, displayTheme]);

  // üî• ÌÖåÎßà Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨ (ThemeProvider + ÏÑ§Ï†ï ÎèôÏãú ÏóÖÎç∞Ïù¥Ìä∏)
  const handleThemeChange = useCallback(async (event: React.ChangeEvent<HTMLSelectElement>) => {
    const newTheme = event.target.value as 'light' | 'dark' | 'system';

    try {
      // 1. Î°úÏª¨ ÏÉÅÌÉú Ï¶âÏãú ÏóÖÎç∞Ïù¥Ìä∏ (UI Î∞òÏùëÏÑ±)
      setDisplayTheme(newTheme);

      // 2. ThemeProvider ÏóÖÎç∞Ïù¥Ìä∏ (Ïã§Ï†ú ÌÖåÎßà Ï†ÅÏö©)
      setTheme(newTheme);

      // 3. ÏÑ§Ï†ï Ï†ÄÏû• (Î∞±ÏóîÎìú ÎèôÍ∏∞Ìôî)
      await updateSetting('app', 'theme', newTheme);

      Logger.info('APP_SETTINGS', 'Theme updated successfully', {
        theme: newTheme,
        source: 'settings_page'
      });
    } catch (error) {
      Logger.error('APP_SETTINGS', 'Failed to update theme', error);

      // üî• ÏóêÎü¨ Ïãú ÏõêÎûò ÏÉÅÌÉúÎ°ú Î°§Î∞±
      setDisplayTheme(settings.theme);
    }
  }, [updateSetting, setTheme, settings.theme]);

  // üî• Ïñ∏Ïñ¥ Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨
  const handleLanguageChange = useCallback((event: React.ChangeEvent<HTMLSelectElement>) => {
    updateSetting('app', 'language', event.target.value);
  }, [updateSetting]);

  // üî• Í∏ÄÍº¥ ÌÅ¨Í∏∞ Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨ - Í∏∞Í∞ÄÏ∞®Îìú Í∞ïÌôîÎ≤ÑÏ†Ñ
  const handleFontSizeChange = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    const size = parseInt(event.target.value, 10);
    if (size >= 10 && size <= 24) {
      try {
        // üî• Ï¶âÏãú CSS Î≥ÄÏàò Ï†ÅÏö©
        document.documentElement.style.setProperty('--app-font-size', `${size}px`);
        document.body.style.fontSize = `${size}px`;

        // üî• ÏÑ§Ï†ï Ï†ÄÏû•
        updateSetting('app', 'fontSize', size);

        Logger.info('APP_SETTINGS', 'üî• Í∏∞Í∞ÄÏ∞®Îìú Ìè∞Ìä∏ ÌÅ¨Í∏∞ Î≥ÄÍ≤Ω ÏôÑÎ£å', {
          newSize: size,
          timestamp: Date.now()
        });
      } catch (error) {
        Logger.error('APP_SETTINGS', 'Ìè∞Ìä∏ ÌÅ¨Í∏∞ Î≥ÄÍ≤Ω Ïã§Ìå®', error);
      }
    }
  }, [updateSetting]);

  // üî• Í∏ÄÍº¥ Ìå®Î∞ÄÎ¶¨ Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨ - Í∏∞Í∞ÄÏ∞®Îìú Í∞ïÌôîÎ≤ÑÏ†Ñ
  const handleFontFamilyChange = useCallback((event: React.ChangeEvent<HTMLSelectElement>) => {
    const selectedFont = event.target.value;

    try {
      // üî• Ï¶âÏãú Ìè∞Ìä∏ Ï†ÅÏö© (useDynamicFontÏùò setFont ÏÇ¨Ïö©)
      setFont(selectedFont);

      // üî• ÏÑ§Ï†ï Ï†ÄÏû•
      updateSetting('app', 'fontFamily', selectedFont);

      Logger.info('APP_SETTINGS', 'üî• Í∏∞Í∞ÄÏ∞®Îìú Ìè∞Ìä∏ Î≥ÄÍ≤Ω ÏôÑÎ£å', {
        newFont: selectedFont,
        timestamp: Date.now()
      });
    } catch (error) {
      Logger.error('APP_SETTINGS', 'Ìè∞Ìä∏ Î≥ÄÍ≤Ω Ïã§Ìå®', error);
    }
  }, [updateSetting, setFont]);

  // üî• ÌÜ†Í∏Ä Ìï∏Îì§Îü¨Îì§
  const handleAutoSaveToggle = useCallback((checked: boolean) => {
    updateSetting('app', 'autoSave', checked);
  }, [updateSetting]);

  const handleStartMinimizedToggle = useCallback((checked: boolean) => {
    updateSetting('app', 'startMinimized', checked);
  }, [updateSetting]);

  const handleMinimizeToTrayToggle = useCallback((checked: boolean) => {
    updateSetting('app', 'minimizeToTray', checked);
  }, [updateSetting]);

  return (
    <div className={SETTINGS_PAGE_STYLES.sectionCard}>
      <div className={SETTINGS_PAGE_STYLES.sectionHeader}>
        <Settings className={SETTINGS_PAGE_STYLES.sectionIcon} />
        <h2 className={SETTINGS_PAGE_STYLES.sectionTitle}>Ïï± ÏÑ§Ï†ï</h2>
      </div>

      <div className={SETTINGS_PAGE_STYLES.settingItem}>
        <SettingItem
          title="ÌÖåÎßà"
          description="Ïï±Ïùò Ïô∏Í¥Ä ÌÖåÎßàÎ•º ÏÑ†ÌÉùÌïòÏÑ∏Ïöî"
          control={
            <select
              value={displayTheme}
              onChange={handleThemeChange}
              className={SETTINGS_PAGE_STYLES.select}
            >
              <option value="system">ÏãúÏä§ÌÖú</option>
              <option value="light">ÎùºÏù¥Ìä∏</option>
              <option value="dark">Îã§ÌÅ¨</option>
            </select>
          }
        />

        <SettingItem
          title="Ïñ∏Ïñ¥"
          description="Ïï±ÏóêÏÑú ÏÇ¨Ïö©Ìï† Ïñ∏Ïñ¥Î•º ÏÑ†ÌÉùÌïòÏÑ∏Ïöî"
          control={
            <select
              value={settings.language}
              onChange={handleLanguageChange}
              className={SETTINGS_PAGE_STYLES.select}
            >
              <option value="ko">ÌïúÍµ≠Ïñ¥</option>
              <option value="en">English</option>
              <option value="ja">Êó•Êú¨Ë™û</option>
            </select>
          }
        />

        <SettingItem
          title="ÏûêÎèô Ï†ÄÏû•"
          description="ÏûëÏóÖ ÎÇ¥Ïö©ÏùÑ ÏûêÎèôÏúºÎ°ú Ï†ÄÏû•Ìï©ÎãàÎã§"
          control={
            <Toggle
              checked={settings.autoSave}
              onChange={handleAutoSaveToggle}
            />
          }
        />

        <SettingItem
          title="ÏµúÏÜåÌôî ÏÉÅÌÉúÎ°ú ÏãúÏûë"
          description="Ïï± ÏãúÏûë Ïãú ÏµúÏÜåÌôîÎêú ÏÉÅÌÉúÎ°ú Ïã§ÌñâÌï©ÎãàÎã§"
          control={
            <Toggle
              checked={settings.startMinimized}
              onChange={handleStartMinimizedToggle}
            />
          }
        />

        <SettingItem
          title="ÏãúÏä§ÌÖú Ìä∏Î†àÏù¥Î°ú ÏµúÏÜåÌôî"
          description="Ï∞ΩÏùÑ Îã´ÏùÑ Îïå ÏãúÏä§ÌÖú Ìä∏Î†àÏù¥Î°ú ÏµúÏÜåÌôîÌï©ÎãàÎã§"
          control={
            <Toggle
              checked={settings.minimizeToTray}
              onChange={handleMinimizeToTrayToggle}
            />
          }
        />

        <SettingItem
          title="Í∏ÄÍº¥ ÌÅ¨Í∏∞"
          description="Ïï±ÏóêÏÑú ÏÇ¨Ïö©Ìï† Í∏ÄÍº¥ ÌÅ¨Í∏∞Î•º ÏÑ§Ï†ïÌïòÏÑ∏Ïöî (10-24px)"
          control={
            <input
              type="number"
              min="10"
              max="24"
              value={settings.fontSize}
              onChange={handleFontSizeChange}
              className={SETTINGS_PAGE_STYLES.numberInput}
            />
          }
        />

        <SettingItem
          title="Í∏ÄÍº¥ Ìå®Î∞ÄÎ¶¨"
          description={
            fontsError
              ? `Ìè∞Ìä∏Î•º Î∂àÎü¨Ïò§ÎäîÎç∞ Î¨∏Ï†úÍ∞Ä ÏûàÏäµÎãàÎã§: ${fontsError}`
              : "Ïï±ÏóêÏÑú ÏÇ¨Ïö©Ìï† Í∏ÄÍº¥ÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî"
          }
          control={
            <select
              value={settings.fontFamily}
              onChange={handleFontFamilyChange}
              className={SETTINGS_PAGE_STYLES.select}
              disabled={fontsLoading}
            >
              {fontsLoading ? (
                <option value="">Ìè∞Ìä∏ Î™©Î°ù Î°úÎî© Ï§ë...</option>
              ) : (
                <>
                  {/* ÎèôÏ†Å Ìè∞Ìä∏ Ïπ¥ÌÖåÍ≥†Î¶¨Î≥Ñ Í∑∏Î£π */}
                  {[...new Set(availableFonts.map(f => f.category))].map(category => {
                    const categoryFonts = availableFonts.filter(f => f.category === category);
                    const categoryLabels: Record<string, string> = {
                      system: 'ÏãúÏä§ÌÖú Ìè∞Ìä∏',
                      korean: 'ÌïúÍµ≠Ïñ¥ Ìè∞Ìä∏',
                      japanese: 'ÏùºÎ≥∏Ïñ¥ Ìè∞Ìä∏',
                      english: 'ÏòÅÏñ¥ Ìè∞Ìä∏'
                    };

                    return (
                      <optgroup key={category} label={categoryLabels[category] || category}>
                        {categoryFonts.map(font => (
                          <option key={font.value} value={font.value}>
                            {font.label}
                          </option>
                        ))}
                      </optgroup>
                    );
                  })}
                </>
              )}
            </select>
          }
        />
        {/* Google account actions (Î°úÍ∑∏ÏïÑÏõÉ Îì±) */}
        <GoogleAccountActions />
      </div>
    </div>
  );
});

AppSettingsSection.displayName = 'AppSettingsSection';
