// üî• ÎèôÏ†Å Ìè∞Ìä∏ ÌõÖ - public/fonts TTF Í∏∞Î∞ò
import { useState, useEffect, useCallback } from 'react';
import { Logger } from '../../shared/logger';

interface UseDynamicFontResult {
    currentFont: string;
    availableFonts: Array<{ value: string; label: string; category: string }>;
    setFont: (family: string) => void;
    loading: boolean;
    error: string | null;
    reload: () => Promise<void>;
}

export function useDynamicFont(): UseDynamicFontResult {
    const [currentFont, setCurrentFont] = useState('');
    const [availableFonts, setAvailableFonts] = useState<Array<{ value: string; label: string; category: string }>>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    // üî• Ìè∞Ìä∏ CSS Ï£ºÏûÖ
    const injectFontCSS = useCallback(async () => {
        try {
            const css = await (window as any).electronAPI?.font?.generateCSS?.();
            if (css) {
                // Í∏∞Ï°¥ ÎèôÏ†Å Ìè∞Ìä∏ Ïä§ÌÉÄÏùº Ï†úÍ±∞
                const existingStyle = document.getElementById('dynamic-fonts');
                if (existingStyle) {
                    existingStyle.remove();
                }

                // Ìè∞Ìä∏ Ï†ïÍ∑úÌôî CSS Ï∂îÍ∞Ä
                const normalizedCSS = css + `
                    /* üî• Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶à Ï†ïÍ∑úÌôî */
                    * {
                        font-size-adjust: 0.5 !important;
                        line-height: 1.6 !important;
                    }
                    
                    /* ÌÖçÏä§Ìä∏ ÏóêÎîîÌÑ∞ ÏòÅÏó≠ Ï†ïÍ∑úÌôî */
                    textarea, input[type="text"], input[type="email"], .text-editor {
                        font-size-adjust: 0.5 !important;
                        line-height: 1.6 !important;
                        vertical-align: baseline !important;
                    }
                    
                    /* ÌäπÏ†ï Ïª¥Ìè¨ÎÑåÌä∏ Ï†ïÍ∑úÌôî */
                    .idea-content, .synopsis-content, .character-content, .notes-content {
                        font-size-adjust: 0.5 !important;
                        line-height: 1.6 !important;
                    }
                    
                    /* Ìè∞Ìä∏ Ìå®Î∞ÄÎ¶¨Î≥Ñ Í∞úÎ≥Ñ Ï°∞Ï†ï */
                    .font-korean { font-size-adjust: 0.48 !important; }
                    .font-english { font-size-adjust: 0.52 !important; }
                    .font-monospace { font-size-adjust: 0.45 !important; }
                `;

                // ÏÉà Ìè∞Ìä∏ Ïä§ÌÉÄÏùº Ï∂îÍ∞Ä
                const style = document.createElement('style');
                style.id = 'dynamic-fonts';
                style.textContent = normalizedCSS;
                document.head.appendChild(style);

                Logger.info('DYNAMIC_FONT', 'Ï†ïÍ∑úÌôîÎêú Ìè∞Ìä∏ CSS Ï£ºÏûÖ ÏôÑÎ£å', { cssLength: normalizedCSS.length });
            }
        } catch (e) {
            Logger.warn('DYNAMIC_FONT', 'Ìè∞Ìä∏ CSS Ï£ºÏûÖ Ïã§Ìå®', e);
        }
    }, []);

    // üî• ÏÇ¨Ïö© Í∞ÄÎä•Ìïú Ìè∞Ìä∏ Î°úÎìú
    const loadAvailableFonts = useCallback(async () => {
        try {
            setLoading(true);
            setError(null);

            // Ìè∞Ìä∏ ÏÑúÎπÑÏä§ Ï¥àÍ∏∞Ìôî
            await (window as any).electronAPI?.font?.initialize?.();

            // ÎèôÏ†Å Ìè∞Ìä∏ + Ï†ïÏ†Å Ìè∞Ìä∏ Ï°∞Ìï©
            const [dynamicFonts, staticFonts] = await Promise.all([
                (window as any).electronAPI?.font?.getAvailableFonts?.() || [],
                (window as any).electronAPI?.font?.getStaticFonts?.() || []
            ]);

            const allFonts = [
                ...staticFonts,
                ...dynamicFonts
            ];

            setAvailableFonts(allFonts);

            // Ìè∞Ìä∏ CSS Ï£ºÏûÖ
            await injectFontCSS();

            Logger.info('DYNAMIC_FONT', 'Ìè∞Ìä∏ Î™©Î°ù Î°úÎìú ÏôÑÎ£å', {
                dynamicCount: dynamicFonts.length,
                staticCount: staticFonts.length,
                totalCount: allFonts.length
            });
        } catch (e) {
            Logger.error('DYNAMIC_FONT', 'Ìè∞Ìä∏ Î°úÎìú Ïã§Ìå®', e);
            setError('Ìè∞Ìä∏Î•º Î∂àÎü¨Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.');

            // Ìè¥Î∞± Ìè∞Ìä∏ Î™©Î°ù
            setAvailableFonts([
                { value: 'system-ui, sans-serif', label: 'ÏãúÏä§ÌÖú Í∏∞Î≥∏', category: 'system' },
                { value: '-apple-system, BlinkMacSystemFont, sans-serif', label: 'Apple ÏãúÏä§ÌÖú', category: 'system' },
            ]);
        } finally {
            setLoading(false);
        }
    }, [injectFontCSS]);

    // üî• Ï¥àÍ∏∞ Î°úÎìú
    useEffect(() => {
        (async () => {
            await loadAvailableFonts();

            // ÌòÑÏû¨ Ï†ÅÏö©Îêú Ìè∞Ìä∏ Í∞êÏßÄ
            const initial = (
                getComputedStyle(document.documentElement).getPropertyValue('--app-font-family') ||
                getComputedStyle(document.body).fontFamily
            ).trim();
            setCurrentFont(initial);
        })();
    }, [loadAvailableFonts]);

    // üî• Ìè∞Ìä∏ Ï†ÅÏö© - Í∏∞Í∞ÄÏ∞®Îìú Í∞ïÌôîÎ≤ÑÏ†Ñ + ÏÇ¨Ïù¥Ï¶à Ï†ïÍ∑úÌôî
    const setFont = useCallback((family: string) => {
        try {
            // üî• CSS Î≥ÄÏàòÏôÄ body ÎèôÏãú Ï†ÅÏö©ÏúºÎ°ú ÍπúÎπ°ÏûÑ Î∞©ÏßÄ
            document.documentElement.style.setProperty('--app-font-family', family);
            document.body.style.fontFamily = family;

            // üî• Ìè∞Ìä∏ Ï†ïÍ∑úÌôî Ïä§ÌÉÄÏùº Ï†ÅÏö©
            const normalizeStyle = `
                font-size-adjust: 0.5 !important;
                line-height: 1.6 !important;
                vertical-align: baseline !important;
            `;

            // üî• Î™®Îì† ÏöîÏÜåÏóê Ï¶âÏãú Ï†ÅÏö© (ÍπúÎπ°ÏûÑ ÏóÜÎäî Ï†ÑÌôò + Ï†ïÍ∑úÌôî)
            const allElements = document.querySelectorAll('*');
            allElements.forEach((element) => {
                if (element instanceof HTMLElement) {
                    element.style.fontFamily = family;
                    // ÌÖçÏä§Ìä∏ ÏûÖÎ†• ÏöîÏÜåÎì§ÏóêÎäî Ï†ïÍ∑úÌôî Ï†ÅÏö©
                    if (element.tagName === 'TEXTAREA' ||
                        element.tagName === 'INPUT' ||
                        element.classList.contains('text-editor') ||
                        element.classList.contains('idea-content') ||
                        element.classList.contains('synopsis-content')) {
                        element.style.fontSizeAdjust = '0.5';
                        element.style.lineHeight = '1.6';
                        element.style.verticalAlign = 'baseline';
                    }
                }
            });

            setCurrentFont(family);
            Logger.info('DYNAMIC_FONT', 'üî• Í∏∞Í∞ÄÏ∞®Îìú Ï†ïÍ∑úÌôî Ìè∞Ìä∏ Ï†ÅÏö© ÏôÑÎ£å', {
                family,
                appliedToElements: allElements.length
            });
        } catch (e) {
            Logger.error('DYNAMIC_FONT', 'Ìè∞Ìä∏ Ï†ÅÏö© Ïã§Ìå®', e);
        }
    }, []);

    // üî• Ìè∞Ìä∏ Î¶¨Î°úÎìú
    const reload = useCallback(async () => {
        try {
            await (window as any).electronAPI?.font?.reload?.();
            await loadAvailableFonts();
        } catch (e) {
            Logger.error('DYNAMIC_FONT', 'Ìè∞Ìä∏ Î¶¨Î°úÎìú Ïã§Ìå®', e);
        }
    }, [loadAvailableFonts]);

    return {
        currentFont,
        availableFonts,
        setFont,
        loading,
        error,
        reload
    };
}

export default useDynamicFont;
